body {
  color: #333;
  background: #f4f4f4 linear-gradient(135deg, #f5f7fa, #c3cfe2);
  margin: 0;
  padding: 0;
  font-family: Roboto, Arial, sans-serif;
  line-height: 1.6;
}

h1, h2 {
  color: #2a5298;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #2a5298;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
}

button:hover {
  background-color: #1e3d70;
}

button {
  transition: background-color .3s, transform .2s;
}

button:hover {
  transform: scale(1.05);
}

header {
  color: #fff;
  text-align: center;
  background: #333;
  padding: 10px 20px;
}

section {
  background: #fff;
  border-radius: 5px;
  margin: 20px;
  padding: 10px;
  box-shadow: 0 2px 5px #0000001a;
}

footer {
  text-align: center;
  color: #fff;
  background: #333;
  margin-top: 20px;
  padding: 10px;
}

.skills ul li {
  background-color: #fff;
  border-radius: 4px;
  margin: 5px 0;
  padding: 5px;
  transition: background-color .3s;
}

.skills ul li:hover {
  cursor: pointer;
  background-color: #f0f0f0;
}

section img {
  border-radius: 10px;
  margin: 0 auto 10px;
  display: block;
  box-shadow: 0 4px 8px #0003;
}

section h2 {
  opacity: 0;
  animation: 1s forwards fadeInUp;
  transform: translateY(20px);
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.skills ul li {
  background-color: #fff;
  border-radius: 4px;
  margin: 10px 0;
  padding: 10px;
  transition: background-color .3s;
  box-shadow: 0 2px 4px #0000001a;
}

form {
  flex-direction: column;
  gap: 10px;
  max-width: 400px;
  margin: 20px auto;
  display: flex;
}

form input, form textarea, form button {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

form button {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  transition: background-color .3s;
}

form button:hover {
  background-color: #555;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

h1 {
  animation: 1.5s ease-out fadeIn;
}

#projects {
  background: #f9f9f9;
  border-radius: 8px;
  max-width: 1200px;
  margin: 40px auto;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

#projects h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

.project-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.project-item {
  text-align: center;
  background: #fff;
  border-radius: 5px;
  flex: calc(33.333% - 20px);
  padding: 15px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 2px 4px #0000001a;
}

.project-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 8px #0003;
}

.project-item h3 {
  color: #007bff;
  margin: 0 0 10px;
}

.project-item p {
  color: #666;
  margin: 0 0 15px;
}

.project-item a {
  color: #fff;
  background: #007bff;
  border-radius: 5px;
  padding: 10px 15px;
  text-decoration: none;
  transition: background .3s;
  display: inline-block;
}

.project-item a:hover {
  background: #0056b3;
}
